<template>
    <div class="detail-home">
        <scroll-view class="wrapper">
            <div id="imgSize">
                <img :src="img" alt="无法打开">
            </div>
            <div id="titles">{{title}}</div>
            <div id="price">
                <i>¥</i>
                <span>{{price}}</span>
                <i>¥</i>
                <span id="orgPrice">
                    <i>{{orgPrice}}</i>
                </span>
            </div>
            <div id="frees">{{free}}</div>
            <div id="gray"></div>
            <div id="averageScores">综合评价：{{comment.commentData.averageScore}}</div>
            <div id="comment" v-for="(item,i) in comment.commentData.list" :key="'comment' +i">
                <div><img :src="item.user.avatar" alt="无法打开" id="userImg"></div>
                <div>
                    <div>{{item.user.uname}}</div>
                    <div>{{item.content}}</div>
                    <div>
                        <span>{{item.time}}</span>
                        <span>{{item.style}}</span>
                    </div>
                </div>
            </div>
        </scroll-view>
        <detail-bottom></detail-bottom>
    </div>
</template>
<script>
import DetailBottom from "../components/DetailBottom.vue";
import { mapState, mapActions, mapMutations } from "vuex";
export default {
    data() {
        let {img,title,price,tradeItemId,orgPrice} = this.$route.query;
        return {
            // query: this.$route.query.e,
            img,
            title,
            price,
            orgPrice,
            orgPrice,
            tradeItemId,
            free: "免运费"           
        };
    },
    components:{DetailBottom},
    computed: {
        ...mapState(["comment"])
    },
    methods: {
        ...mapActions(["loadcommenthData"])
    },
    mounted() {
        this.loadcommenthData({ itemId: this.tradeItemId });
    }
};
</script>

<style lang="less" scoped>
@import url("../less/less-var");
.detail {
    &-home {
        > .wrapper {
            position: absolute;
            top: @0px; /*px*/
            bottom: @0px; /*px*/
            left: 0px;
            right: 0px;
            overflow: hidden;
            > div {
                width: 90%;
                margin: 20px auto;
            }
            div#imgSize {
                width: 100%;
                text-align: center;
                margin-top: 40px;
            }
            #titles {
                width: 100%;
                height: 50px;
                margin: 40px auto;
                line-height: 50px;
                font-size: 30px; /*px*/
                text-align: center;
            }
            #price {
                height: 50px;
                margin-left:50px;
                margin-top:50px;
                > span {
                    font-size: 55px; /*px*/
                }
                > i {
                    font-size: 25px; /*px*/
                }
                > #orgPrice {
                    font-size: 25px; /*px*/
                    text-decoration: line-through;
                }
            }
        }
    }
}
#frees {
    color: #ccc;
    margin-left:50px;
}
#gray {
    height: 30px;
    margin: 10px 0px;
    background: @light-gray;
}
#averageScores {
    height: 50px;
    margin: 30px 50px;
    color: @red;
    font-size: 30px;
    font-weight: bold;
}
#comment {
    display: flex;
    // justify-content: center;
    height: 350px; //dpr
    > div {
        margin-left: 50px;
        font-size: 25px; /*px*/
        > div {
            line-height: 50px;
        }
    }
}
#userImg {
    width: 60px;
    height: 60px; //dpr
}
</style>


