<template>
    <!-- start 底部导航 -->
    <div class="bottomNavBar">
        <div id="md-bottom-bar">
            <router-link v-for="(item,index) in tabArr" :to="item.to" :key="index" v-on:click.native="hid(index)">
                <div id="md-bottom-bar-item">
                    <i :class="'icon iconfont '+item.iconClass"></i>
                    <div>{{item.title}}</div>
                </div>
            </router-link>
        </div>
    </div>
</template>
<!-- end 底部导航 -->
<script>
export default {
    data() {
        return {
            selectedIndex: 0,
            tabArr: [
                {
                    to: "/home",
                    iconClass: "icon-shouye",
                    title: "首页"
                },
                {
                    to: "/types",
                    iconClass: "icon-fenlei",
                    title: "分类"
                },
                {
                    to: "/buy",
                    iconClass: "icon-gouwuche",
                    title: "购物车"
                },
                {
                    to: "/me",
                    iconClass: "icon-tubiaolunkuo-",
                    title: "我的"
                }
            ]
        };
    },
    methods:{
        hid(index){
            this.selectedIndex = index;
            
        }
    },
    mounted() {
        this.$root.$on("loadCart", e => {
            this.selectedIndex = e;
            console.log(e);
        });
    }
};
</script>
<style lang="less">
@import "../../static/icon/iconfont.css";
@import url("../less/less-var");
@iconFont: 50px;
@word: 30px;
@top: 40px;
 
.bottomNavBar {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: @bottomHeight;/*px*/
    background-color: @ltgray;
    border-top:@border @light-gray;
}
#md-bottom-bar-item {
    color:@black;
    text-align: center;
    .icon {
        font-size: @iconFont; /*px*/
        outline: none;
    }
    div {
        font-size: @word; /*px*/
    }
}
#md-bottom-bar {
    display: flex;
    justify-content: space-around;
    height: 100px; // dpr 适配时
    margin-top: 32px;
}
a:-webkit-any-link {
    color:  @white;
    line-height: 40px;
}
.active>div>.icon{
    font-size:60px!important;
    line-height: 80px;
    color:@red;
}
.active>div>div{
    display: none;
}
</style>



