<template>
    <div class="shop-home">
        <scroll-view class="wrapper">
            <div id="shopContent">
                <div v-for="(data,i) in type.shopList" :key="'shopList ' +i" class="shopList" @click="goSearch(data.title)">
                    <img :src="data.image" alt="无法打开">
                    <div>{{data.title}}</div>
                </div>
            </div>
            <appoint-content :appointList="type.appointList"></appoint-content>
            <guide-like :guideLists="type.appointList.contentList"></guide-like>
        </scroll-view>
    </div>
</template>
<script>
import AppointContent from "./AppointContent";
import GuideLike from "../homeComponents/GuideLike"
import { mapState, mapActions, mapMutations } from "vuex";
export default {
    data() {
        return {
           
        };
    },
    components:{AppointContent,GuideLike},
    computed: {
        ...mapState(["type"])
    },
    methods: {
        ...mapActions(["loadListData", "loadappointData"]),
        goSearch(e){
            this.$router.push({path:"/Search",query:{q:e}});
        }
    },
    mounted() {
        this.loadListData(3627);
        this.loadappointData({ fcid:"10062603", page: 1, sort:'pop'});
    }
};
</script>
<style lang="less">
@import url("../../less/less-var");

.shop-home {
    > .wrapper {
        position: absolute;
        top: @topHeight; /*px*/
        bottom: @bottomHeight; /*px*/
        width: 75%;
        left: 25%;
        right: 0px;
        overflow: hidden;
        #shopContent {
            display: flex;
            justify-content: center;
            -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
            text-align: center;
            .shopList {
                width: 30%;
                height: 230px;
                margin-top: 40px;
                img {
                    width: 80%;
                }
                div {
                    padding: 20px;
                }
            }
        }
    }
}
</style>


