<template>
    <div>
        <TopSearchBar></TopSearchBar>
        <search-compoent :searchList="search.search"></search-compoent>
        <guide-like :guideLists="search.search.contentList"></guide-like>
    </div>
</template>
<script>
import { mapState, mapActions, mapMutations } from "vuex";
import TopSearchBar from "../components/TopSearchBar";
import searchCompoent  from "../components/searchCompoents/searchCompoent";
import GuideLike from "../components/homeComponents/GuideLike";
export default {
    data() {
        return {};
    },
    components: { TopSearchBar,searchCompoent,GuideLike},
    computed: {
        ...mapState(["search"])
    },
    methods: {
          ...mapActions(["loadSearchData"])
    },
    mounted(){
        console.log(this.$route.query.q)
        this.loadSearchData({q:this.$route.query.q,page:1,sort:"pop"});
    }
};
</script>
<style lang="less">
@import url("../less/less-var");
</style>


