<template>
    <div class="sales-box">
        <div v-for="(data,i) in salesList" :key="i" :class="'sales-list sales-one'+ i">
            <div>
                <b>{{data.title}}</b>
            </div>
            <div>{{data.viceTitle}}</div>
            <img :src="data.image">
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {};
    },
    props: {
        salesList: { type: Array, default: [] }
    },
    methods: {}
};
</script>
<style lang="less" scoped>
@import url("../../less/less-var");
.sales {
    &-box {
        display: flex;
        justify-content: center;
        -webkit-flex-wrap: wrap;
        flex-wrap: wrap;
        height: 470px; // dpr 适配时
        position: relative;
    }
    &-list {
        text-align: center;
        width: 50%;
        height: 320px; // dpr 适配时
        border: @border #ccc;
        box-sizing: border-box;
        div:first-child {
            margin-top: 15px;
            font-size: 30px; /*px*/
        }
        div:nth-child(2) {
            margin-top: 20px;
            color: @light-red;
        }
        img {
            width: 160px;
            margin: 15px -30px;
            height: 140px; // dpr 适配时
        }
    }
    &-one0 {
        width: 50%;
        position: relative;
        div {
            position: absolute;
            top: 30px;
            left: 38%;
        }
        div:nth-child(2) {
            margin: 50px -50px;
        }
        img {
            position: absolute;
            height: 200px !important; // dpr 适配时
            left: 38%;
            bottom: -14px;
        }
    }
    &-one1 {
        width: 50%;
        height: 160px; // dpr 适配时
        position: relative;
        div {
            position: absolute;
            left: 20px;
            top: 40px;
        }
        div:nth-child(2) {
            margin-top: 50px;
        }
        img {
            position: absolute;
            right: 20px;
        }
    }
    &-one2 {
        width: 50%;
        height: 160px; // dpr 适配时
        position: absolute;
        top: 160px;
        left: 50%;
        div {
            position: absolute;
            left: 20px;
            top: 40px;
        }
        div:nth-child(2) {
            margin-top: 50px;
        }
        img {
            position: absolute;
            right: 20px;
        }
    }
    &-one3 {
        width: 33.3%;
        height: 240px; // dpr 适配时
    }
    &-one4 {
        width: 33.3%;
        height: 240px; // dpr 适配时
    }
    &-one5 {
        width: 33.3%;
        height: 240px; // dpr 适配时
    }
}
</style>


