<template>
    <div class="history-home">
        <div class="clear">
            <div>
                <div>
                    <span class="icon iconfont icon-sousuo"></span>
                    <span>历史搜索</span>
                </div>
                <div id="deleteImg" @click="deletes"><img :src="deleteImg" alt="无法打开"></div>
            </div>
            <div ref="emptys">
                <div v-for="(data,i) in historyList" :key="'historyList '+i" class="historyList">
                    <span @click="historySearch(data)">{{data}}</span>
                </div>
            </div>
        </div>
      
    </div>
</template>
<script>
export default {
    data() {
        return {
            deleteImg: "./static/img/delete.png"
        };
    },
    props: {
        historyList: { type: Array, default: [] }
    },
    methods: {
        deletes() {
            localStorage.removeItem("historyWord");
            this.$refs.emptys.innerHTML = "";
        },
        historySearch(e){
            // console.log(e)
            this.$router.push({path:"/Search",query:{q:e}});
        }
    }
};
</script>
<style lang="less">
@import url("../../less/less-var");
@55px: 55px;
.history {
    &-home {
        .clear::after {
            content: "";
            clear: both;
            display: block;
        }
        > div:first-child {
            border-bottom: @border @light-gray;
            min-height: 20px;
            > div { 
                position: relative;
                > div {
                   margin: 30px 40px;
                    font-size: 25px; /*px*/
                    width: 100%;
                    height: 20px; //dpr
                    color: @gray;
                }
                >#deleteImg {
                        width: 35px;
                        height: 50px; /*px*/
                        color: @gray;
                        margin-top: 13px;
                        position: absolute;
                        top:-30px;
                        right:@0px;
                    }
            }
            > div {
                > .historyList {
                    width: fit-content;
                    width: -webkit-fit-content;
                    width: -moz-fit-content;
                    height: @55px;
                    margin: 20px;
                    text-align: center;
                    line-height: @55px;
                    font-size: 20px; /*px*/
                    border: @border #ddd;
                    float: left;
                }
            }
        }
       
    }
}
</style>


