<template>
    <div class="hot-box">
        <div class="title">—— ◆ 热门分类 ◆——</div>
        <div class="content">
            <div v-for="(data,i) in hotLists" :key="'data '+i" class="hot-list">
                <img :src="data.image" alt="无法打开" class="hot-img">
                <div class="hot-title">{{data.title}}</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {};
    },
    props: {
        hotLists: { type: Array, default: [] }
    },
    methods: {}
};
</script>
<style lang="less">
.hot {
    &-box {
        position:relative;
        .title {
            width: 100%;
            font-size: 32px !important; /*px*/
            position: absolute;
            left: 30%;
            top:-20px;
        }
        .content{
            margin-top:89px;
            height: 800px; // dpr 适配时
            display: flex;
            justify-content: center;
            -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
            .hot-list{
            width:25%;
            height: 200px; //dpr 适配时
            text-align:center;
                img {
                    width:85%;
                    height: 135px; // dpr 适配时
                    margin-top:50px;
                }
                div {
                    margin:10px 0;
                }
            }
        }
      
    }
}
</style>


