<template>
    <div class="bottomNavBar">
        <div id="shoucang">
            <star-component></star-component>
            <div>{{house}}</div>
        </div>
        <div>{{store}}</div>
        <div class="buy">{{joinshop}}</div>
        <div class="buy" id="buyQuick">{{nowbuy}}</div>
    </div>
</template>
<script>
import StarComponent from "../globalComponents/StarComponent.vue";
export default {
    data() {
        return {
            house:"收藏",
            store:"小店",
            joinshop:"加入购物车",
            nowbuy:"立即购买"
        };
    },
    components:{StarComponent},
    methods: {}
};
</script>
<style lang="less" scoped>
@import url("../less/less-var");
.bottomNavBar {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: @bottomHeight;/*px*/
    background-color: @ltgray;
    border-top:@border @light-gray;
    display: flex;
    >div{
       flex-grow: 1;
       border: @border @light-gray;
       box-sizing: border-box;
       text-align: center;
       font-size:35px;/*px*/
       line-height: 130px;/*px*/
    }
    >#shoucang{
        line-height: 50px;
    }
    .buy{
        flex-grow: 1.2;
        background: #ffe6e8;
        color:#ff5777;
    }
    #buyQuick{
        background: #ff5777;
        color:@white;
    }
}
</style>


