import jsonpUtil from '../until/jsonpUtil'

const m_multiget = "https://mce.mogucdn.com/jsonp/multiget/3";
const search_multiget = "https://list.mogujie.com/search";
const type_multiget = "https://mce.mogucdn.com/jsonp/multiget/3";
const list_multiget = "https://mce.mogucdn.com/jsonp/get/3";
const appoint_multiget = "https://list.mogujie.com/search";
const hot_multiget = " https://list.mogujie.com/module/mget";
const allSearch_multiget= "https://list.mogujie.com/search";
const comment_multiget="https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";


const error = function (error) {
    // alert(error.message);
    console.log(error)
}


//请求方法
export default {
    // { pids: [1,2,3], sort: 213,  }
    loadData(url, data) {
        //参数常量
        for (var key in data) {
            var value = data[key];
            if (Array.isArray(value)) {
                data[key] = value.toString();
            } else {
                data[key] = value;
            }
        }
        return jsonpUtil.request(url, data)
            .then((response) => {
                if (response.ok) {
                    return Promise.resolve(response.json());
                } else {
                    return Promise.reject(new Error("请求失败"));
                }
            })
    },

    loadDataByPids(pids) {
        return this.loadData(m_multiget, {
            appPlat: "m",
            pids: pids
        });
    },

    loadDataByLike(pid, sort, cKey) {
        return this.loadData(search_multiget, {
            pid: pid,
            sort: sort,
            cKey: cKey

        });
    },

    loadDataByType(pids) {
        return this.loadData(type_multiget, {
            appPlat:"m",
            pids:pids
        });
    },
  
    loadDataByList(pid) {
        return this.loadData(list_multiget, {
            appPlat:"m",
            pid:pid
        });
    },
    loadDataByappoint(prams) {
        prams.cKey = 'h5-cube';
        return this.loadData(appoint_multiget,prams);
    },
    loadDataBysort(hotData){ 
        hotData.platform="H5";
        hotData.code="hotWord,sketch";
        return this.loadData( hot_multiget,hotData);
    },
    loadDataBysearch(searchData){ 
        searchData.cKey=46;
        searchData.sort="pop";
        return this.loadData(allSearch_multiget,searchData);
    },
    loadDataByComment(commentData){ 
        commentData.type=1;
        commentData.appPlat="m";
        return this.loadData(comment_multiget,commentData);
    }
}


