<template>
    <div class="gudie-box">
        <div class="gudie-content">
            <div v-for="(data,i) in guideLists" :key="'guideLists'+i" class="guideType" @click="goDetail(data)">
                <img :src="data.img" alt="无法打开" id="shop">
                <div id="title_price">
                    <div id="title">{{data.title}}</div>
                    <div id="price">
                        <span>
                            <b>{{data.price}}</b>
                        </span>
                        <span id="cfav">{{data.cfav}}</span>
                        <StarComponent class="star"></StarComponent>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {};
    },
    props:["guideLists"],
    methods: {  
        goDetail(e){
            let {img,title,price,tradeItemId,orgPrice} = e;
            console.log(e)
             this.$router.push({path:"/Detail",query:{img,title,price,tradeItemId,orgPrice}});

        }
    }
};
</script>
<style lang="less">
@import url("../../less/less-var");
.gudie {
    &-box {
        position: relative;
        .gudie-content {
            display: flex;
            background-color: white;
            -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
            text-align: center;
            .guideType {
                width: 47.5%;
                height: 550px; // dpr 适配时
                margin: 0 5.5px;
                #shop {
                    width: 94%;
                    height: 400px; // dpr 适配时
                }
                #title_price {
                    width: 96%;
                    text-indent: 15px;
                    height: 100px; // dpr 适配时
                    margin-top:10px;
                    #title {
                        line-height: 30px;
                        height: 57px; // dpr 适配时
                        font-size: 20px; /*px*/
                        overflow: hidden;
                        color:@gray-dark;
                    }
                    #price {
                        height: 30px; // dpr 适配时
                        text-align:left;
                        margin-top:20px;
                        position: relative;
                       #cfav{
                           position:absolute;
                           right:60px;
                            font-size: 20px; /*px*/
                       }
                       .star{
                           position: absolute;
                           right:0px;
                           top:-15px;
                       }
                    }
                }
            }
        }
    }
}

</style>


