<template>
    <div class="assort-box">
        <div v-for="(data,i) in assortList" :key="i" class="assort-list">
            <img :src="data.image">
            <div>{{data.title}}</div>
        </div>
         <div class="assort-preferential">——— ◆  超市惠 促销直达 ◆  ———</div>
    </div>
</template>
<script>
export default {
    data() {
        return {
        };
    },
    props:{
        assortList:{ type: Array, default: [] }
    },
    methods: {}
};
</script>
<style lang="less" scoped>
.assort {
    &-box {
        display: flex;
        justify-content: center;
        -webkit-flex-wrap: wrap;
        flex-wrap: wrap;
    }
    &-list {
        text-align: center;
        width: 20%;
        height: 150px; // dpr 适配时
        font-size:25px; /*px*/
        margin-top:12px; 
        img {
            width: 70%;
            height: 100px; // dpr 适配时
            margin: 12px 0 12px;
            border-radius: 50px;
        }
        div{
            margin:5px 0 10px;
        }
    }
    &-preferential{
        font-size:30px;/*px*/
        margin:35px;
    }
}
</style>


