import api from '../../server/api';

const state = {
    typeList: [],
    shopList: [],
    appointList: {},
}


const getters = {

}

const actions = {
    loadTypeDatas({ commit }) {
        api.loadDataByType(117330)
            .then((data) => {
                commit("setType", data.data["117330"].list)
                // console.log(data.data["117330"].list)
            }).catch(err => {
                alert(err.message);
            })
    },
    loadListData({ commit }, pid = 3627) {
        api.loadDataByList(pid)
            .then((data) => {
                commit("setShop", data.data.list);
                // console.log(data)

            }).catch(err => {
                alert(err.message);
            })
    },
    loadappointData({ commit },prams){
        api.loadDataByappoint(prams)
            .then((data) => {
                commit("setAppoint", {titleList:data.result.sortFilter,contentList:data.result.wall.docs})
                // console.log(data.result.sortFilter);
            }).catch(err => {
                // alert(err);
                console.log(err);
            })
    }
   
}
const mutations = {
    setType(state, data) {
        state.typeList = data;
    },
    setShop(state, data) {
        state.shopList = data;
    },
    setAppoint(state, data) {
        state.appointList = data;
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}


