<template>
    <div class="type-home">
        <TopNavBar></TopNavBar>
        <type-list :typeList="type.typeList"></type-list>
        <type-shop></type-shop>
        <bottom-nav-bar></bottom-nav-bar>
    </div>
</template>
<script>
import TopNavBar from "../components/TopNavBar";
import TypeList from "../components/TypeListComponents/TypeList";
import TypeShop from "../components/TypeListComponents/TypeShop";
import BottomNavBar   from "../components/BottomNavBar";
import { mapState, mapActions, mapMutations } from "vuex";
export default {
    data() {
        return {};
    },
    components: { TopNavBar, TypeList, TypeShop,BottomNavBar},
    computed: {
        ...mapState(["type"])
    },
    methods: {
        ...mapActions(["loadTypeDatas"])
    },
    mounted() {
        this.loadTypeDatas();
    }
};
</script>
<style lang="less">
</style>


