<template>
    <div class="list-home">
        <scroll-view class="wrapper">
            <div id="listContent">
                <div v-for="(data,i) in  typeList" :key="'typeList ' +i" @click="change(i,data.maitKey,data.miniWallkey)" :class="data.maitKey">
                    <div class="listTitle" :class="{ active:isactive == i}">
                        <p :class="{ borders:isactive == i}"><b>{{data.title}}</b></p>
                    </div>
                </div>
            </div>
        </scroll-view>
    </div>
</template>
<script>
import { mapState,mapActions,mapMutations } from 'vuex'
export default {
    data() {
        return {
            isactive: 0,
            maitKey:3627,
            miniWallkey:10062603
        };
    },
    props: {
        typeList: { type: Array, default: [] }
    },
    computed: {
        ...mapState(["type"])
    },
    methods:{
      ...mapActions(["loadListData","loadappointData"]),
      change(i,e,f) {
            this.isactive = i;
            this.maitKey = e;
            this.miniWallkey = f;
            // console.log(this.maitKey,this.miniWallkey);
               this.loadListData(this.maitKey);
               this.loadappointData({ fcid:this.miniWallkey, page: 1, sort: 1 });
            this.$root.$emit("loadminiWallkey",this.miniWallkey);
        }
    }
};
</script>
<style lang="less" scoped>
@import url("../../less/less-var");
.active {
    color: red;
    background: #fff;
}
.borders {
    border-left: 5px solid red;
}

.list {
    &-home {
        > .wrapper {
            position: absolute;
            top: @topHeight; /*px*/
            bottom: @bottomHeight; /*px*/
            width: 25%;
            background: @min-gray;
            left: 0px;
            right: 0px;
            overflow: hidden;
            #listContent {
            width: 100%;
            div {
                width: 100%;
            }
            .listTitle {
                width: 100%;
                height: 140px; //dpr
                line-height: 90px;
                text-align: center;
                font-size: 25px; /*px*/
                position: relative;
                p {
                    position: absolute;
                    text-align: center;
                    width: 100%;
                    height: 40px; //dpr
                    line-height: 55px;
                    top: 35px;
                }
            }
        }
        }
       
    }
}
</style>


