import api from '../../server/api';

const state = {
    hotList: {},
    search:{}
}
const getters = {

}

const actions = {
    loadHotData({ commit },hotData) {
        api.loadDataBysort(hotData)
            .then((data) => {
                commit("setHot",{keyWorld:data.data.sketch.data.frontword,searchList:data.data.hotWord.data})
                // console.log(data.data.hotWord.data);
            }).catch(err => {
                console.log(err);
            })
    },
    loadSearchData({ commit },searchData) {
        api.loadDataBysearch(searchData)
        .then((data) => {
            commit("setSearch",{titleList:data.result.sortFilter,contentList:data.result.wall.docs})
            // console.log(data.result.wall.docs);
        }).catch(err => {
            console.log(err);
        })
    }
}

const mutations = {
    setHot(state, data){
        state.hotList = data;
    },
    setSearch(state, data){
        state.search = data;
    }
}
export default {
    state,
    getters,
    actions,
    mutations
}


