<template>
    <div class="clearSize" @click="starClick">
        <div>
            <img :src="!flag?openStarUrl:closeStarUrl">
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            openStarUrl: "./static/img/strart-open.png",
            closeStarUrl: "./static/img/start-close.png",
            flag:true
        };
    },
    methods: {
        starClick(){
            this.flag=!this.flag;
        }
    },
    mounted(){
    }
};
</script>
<style lang="less">
.clearSize {
    font-size: 0px;
    img {
        width: 45px;
    }
}
</style>


