<template>
    <div class="swiper-box">
        <swiper :options="swiperOption">
            <swiper-slide v-for="(v,i) in swiperList" :key="'swiper'+i">
                <img :src="v.image_800" alt="无法打开">
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>
<script>
export default {
    data() {
        return {
            swiperOption: {
                autoplay: {
                    delay: 3000,
                    stopOnLastSlide: false,
                    disableOnInteraction: false
                },

                pagination: {
                    el: ".swiper-pagination"
                }
            }
        };
    },
    props: {
        swiperList: { type: Array, default: [] }
    },
    methods: {}
};
</script>
<style lang="less">
.swiper {
    &-box {
        height: 270px; // dpr
        .swiper-container{
            height: 270px; // dpr
        }
    }
    &-pagination {
        bottom: 20px;
        left: 0;
        width: 100%;
        
    }
    
}
 
</style>


