<template id="header">
    <!-- start 头部搜索框 -->
    <div id="nav-box">
        <div id="navbar">
            <i class="icon iconfont icon-xiangzuojiantou" @click="topPage"></i>
            <div id="searchList">
                <input type="search"  :placeholder="this.$route.query.q"  ref="holder">
                <span class="icon iconfont icon-sousuo" id="iconSearch" v-show="!flag"></span>
            </div>
            <i class="icon iconfont icon-gouwuche" v-show="!flag" @click="shopCart"></i>
        </div>
        
    </div>
    <!-- end 头部搜索框 -->
</template>
<script>

export default {
    data() {
        return {
            flag: false,
            selectIndex: "2",
            history:[]
        };
    },
    components:{},
   
    methods: {
        shopCart() {
            this.$root.$emit("loadCart",this.selectIndex);
            this.$router.push({
                path: "buy"
            });
        },
        topPage(){
            this.$router.back();
        }
    },
    mounted() {
        
    }
};
</script>
<style lang="less" scoped>
@import url("../less/less-var");
@height: 100px;
@i_fz: 50px;
@search_fz: 12px;
@b_radius: 5px;
@search_m: 20px @0px;
@search_indent: 55px;
@search_lh: 26px;
@iconSearch_fz: 36px;
@iconSearch_top:40px;
@iconSearch_left: 5px;
@content_bottom: 56px;
@searchWord_fz: 34px;
#navbar {
    width:100%;
    display: flex;
    justify-content: center;
    height: @height;/*px*/  
    flex-direction: row;
    flex-wrap: nowrap;
    background: @orange-dark;
    color: @white;
}
#navbar > i {
    font-size: @i_fz;
    flex-grow: 1;
    text-align: center;
    line-height: @height;  
}
#searchList {
    position: relative;
    flex-grow: 4;
    font-size: @search_fz;/*px*/
}
input[type="search"] {
    margin: @search_m; 
    border-radius: @b_radius;/*no*/
    border: @border @white;/*no*/
    outline: none;
    text-indent: @search_indent;
    line-height: @search_lh; 
    width: 100%;
    height:60px;// dpr
    background:@white;
    font-size:28px;/*px*/
}
#iconSearch {
    font-size: @iconSearch_fz;/*px*/
    position: absolute;
    top: @iconSearch_top; 
    left: @iconSearch_left; 
    color: black;
}
</style>




