<template>
    <div id="hotSearch">
        <img :src="closeStarUrl">
        <span>热门搜索</span>
        <div id="title">
            <div v-for="(data,i) in hot" :key="'hot '+i" :class="'hotRed'+i">
                <div @click="HotList(data.frontword)">{{data.frontword}}</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            closeStarUrl: "../../static/img/start-close.png"
        };
    },
    props:["hot"],
    methods: {
        HotList(e){
            this.$router.push({path:"/Search",query:{q:e}});
        }
    }
};
</script>
<style lang="less" scoped>
@import url("../../less/less-var");
.hotRed0,.hotRed3,.hotRed6{
    color:@red;
}
#hotSearch {
    margin: 35px 30px;
    font-size: 27px; /*px*/
    color: @gray;
    > img {
        width: 40px;
        height: 40px; /*dpr*/
        color: @gray;
    }
}
#title{
    display: flex;
    justify-content: center;
    flex-wrap:wrap;
    >div{
        margin:20px;
        width:80px;
        height: 60px;
        border:@border @light-gray;
        font-size:12px;/*px*/
        text-align: center;
        line-height: 60px;
    }
}
</style>


