<template>
    <div class="appoint-home">
        <div>
            <div class="appointList">
                <div v-for="(datas,i) in appointList.titleList" :key="'appointList ' +i" class="appoint-list" :class="{activeApp:isAPP == i}" @click="changeAPP(i,datas.sortKey)">
                    {{datas.title}}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
    data() {
        return {
           isAPP:0,
           miniWallkey:"10062603",
           sortKey:""
        };
    },
    props: {
        appointList: { type: Object, default: {} }
    },
    computed: {
        ...mapState(["type"])
    },
    methods: {
        ...mapActions(["loadappointData"]),
        changeAPP(i,e){
            this.isAPP = i;
            this.sortKey = e;
            this.loadappointData({fcid:this.miniWallkey, page:1,sort:this.sortKey});
        }     
    }, 
    mounted(){
        this.$root.$on("loadminiWallkey",(e)=>{
            this.miniWallkey=e;
        })
    }
};
</script>
<style lang="less">
@import url("../../less/less-var");
.activeApp{
    color:@red;
}
.appoint {
    &-home {
        margin: 0px 10px;
        .appointList {
            height: 100px; //dpr
            border-top: @border @light-gray;
            border-bottom: @border @light-gray;
            line-height: 100px;
            margin-bottom: 10px;
            display: flex;
            justify-content: space-around;
            > .appoint-list {
                height: 30px; //dpr
                font-size: 25px;/*px*/
            }
        }
    }
}
</style>


