import api from '../../server/api';

const state = {
    swiperList:[],
    assortList:[],
    salesList:[],
    hotLists:[],
    guideLists:[]
}

const getters = {
     
}

const actions = {
    loadHomeDatas({ commit }) {
        api.loadDataByPids([122224, 106930, 51833, 51836])
        .then((data) => {
            commit("setWarpList", data.data["122224"].list)
            commit("setTypes", data.data["106930"].list)
            commit("setBebefit", data.data["51833"].list)
            commit("setHotType", data.data["51836"].list)
        }).catch(err => {
            alert(err.message);
        })

        api.loadDataByLike("9750","pop",46)
        .then((data)=>{
            commit("setLikes", data.result.wall.docs)
            // console.log(data)
        }).catch(err => {
           alert(err.message);
        })
    }
}

const mutations = {
    setWarpList(state,data){
        state.swiperList=data;
    },
    setTypes(state,data){
        state.assortList=data;
    },
    setBebefit(state,data){
        state.salesList=data;
    },
    setHotType(state,data){
        state.hotLists=data;
    },
    setLikes(state,data){
        state.guideLists=data;
    }
}
export default {
    state,
    getters,
    actions,
    mutations
}


