<template id="header">
    <!-- start 头部搜索框 -->
    <div id="nav-box">
        <div id="navbar">
            <i class="icon iconfont icon-xiaoxizhongxin" v-show="!flag"></i>
            <i class="icon iconfont icon-xiangzuojiantou" v-show="flag" @click="flag=false"></i>
            <div id="searchList">
                <input type="search" @focus="flag=true" v-model="userSearch" :placeholder="search.hotList.keyWorld" ref="holder">
                <span class="icon iconfont icon-sousuo" id="iconSearch" v-show="!flag"></span>
            </div>
            <i class="icon iconfont icon-gouwuche" v-show="!flag" @click="shopCart"></i>
            <span v-show="flag" id="searchWord" @click="searchContent">搜索</span>
        </div>
        <div id="searchContent" v-show="flag">
            <History :historyList="history"></History>
            <hot-list :hot="search.hotList.searchList"></hot-list>
        </div>   
    </div>
    <!-- end 头部搜索框 -->
</template>
<script>
import { mapState, mapActions, mapMutations } from "vuex";
import History from "../components/searchCompoents/History";
import HotList from "../components/searchCompoents/HotList";
export default {
    data() {
        return {
            flag: false,
            userSearch: "",
            selectIndex: "2",
            history:[]
        };
    },
    components:{History,HotList},
    computed: {
        ...mapState(["search"])
    },
    methods: {
        ...mapActions(["loadHotData"]),
         searchContent() {
            if(this.userSearch==""){
                var placeholders=this.$refs.holder.getAttribute("placeholder"); 
                this.userSearch=placeholders;
            }
            this.history.push(this.userSearch); 
            console.log(JSON.stringify(this.history));
            localStorage.setItem("historyWord",JSON.stringify(this.history));

            this.$router.push({path:"/Search",query:{q:this.userSearch,userSearch: this.userSearch}});
        },
        shopCart() {
            this.$root.$emit("loadCart",this.selectIndex);
            this.$router.push({
                path: "buy"
            });
        },
        loadkey(){
            let sea = localStorage.getItem("historyWord") || "[]";
            this.history=JSON.parse(sea);
        }
    },
    mounted() {
        this.loadHotData({});
        this.loadkey();
    }
};
</script>
<style lang="less" scoped>
@import url("../less/less-var");

@height: 100px;
@i_fz: 50px;
@search_fz: 12px;
@b_radius: 5px;
@search_m: 20px @0px;
@search_indent: 55px;
@search_lh: 40px;
@iconSearch_fz: 36px;
@iconSearch_top:40px;
@iconSearch_left: 5px;
@content_bottom: 56px;
@searchWord_fz: 34px;
#navbar {
    width:100%;
    display: flex;
    justify-content: center;
    height: @height;/*px*/  
    flex-direction: row;
    flex-wrap: nowrap;
    background: @orange-dark;
    color: @white;
}
#navbar > i {
    font-size: @i_fz;
    flex-grow: 1;
    text-align: center;
    line-height: @height;/*px*/ 
}
#searchList {
    position: relative;
    flex-grow: 4;
    font-size: @search_fz;/*px*/
    top:0px;/*px*/
}
input[type="search"] {
    margin: @search_m; 
    border-radius: @b_radius;/*no*/
    border: @border @white;/*no*/
    outline: none;
    text-indent: @search_indent;
    line-height: @search_lh; /*px*/ 
    width: 100%;
    height:60px;/*px*/
    background:@white;
    font-size:28px;/*px*/
}
#iconSearch {
    font-size: @iconSearch_fz;/*px*/
    position: absolute;
    top: @iconSearch_top; 
    left: @iconSearch_left; 
    color: black;
}

/* 点击导航 */
#searchContent {
    z-index: 99;
    position: absolute;
    top:@topHeight;/*px*/
    bottom: @0px;
    left: @0px;
    right: @0px;
    background:@white;
}
#searchWord {
    font-size: @searchWord_fz;/*px*/
    flex-grow: 1;
    text-align: center;
    line-height: @height;   
}
</style>




