import api from '../../server/api';

const state = {
    commentData:{}
}

const getters = {
     
}

const actions = {
    loadcommenthData({ commit },commentData) {
        api.loadDataByComment(commentData)
        .then((data) => {
            console.log(data)
            commit("setCommentList",{averageScore:data.data.averageScore,list:data.data.list});
          
        }).catch(err => {
            console.log(err);
        })
    }
}

const mutations = {
    setCommentList(state,data){
        state.commentData=data;
    } 
}
export default {
    state,
    getters,
    actions,
    mutations
}


