<template>
    <div>
        <div class="home-box">
            <TopNavBar></TopNavBar>
            <scroll-view class="wrapper">
                <SowingMap :swiperList="home.swiperList">
                </Sowingmap>
                <div class="home-list">
                    <Assortment :assortList="home.assortList"></Assortment>
                </div>
                <div class="home-cuxiao">
                    <sales-promotion :salesList="home.salesList"></sales-promotion>
                </div>
                <div class="home-hotlist">
                    <hot-list :hotLists="home.hotLists"></hot-list>
                </div>
                <div class="home-like">
                     <div class="titles">—— ◆ 猜你喜欢 ◆ ——</div>
                    <guide-like :guideLists="home.guideLists"></guide-like>
                </div>
            </scroll-view>
        </div>
        <bottom-nav-bar></bottom-nav-bar>
    </div>

</template>
<script type="es6">
import TopNavBar from "../components/TopNavBar";
import SowingMap from "../components/homeComponents/SowingMap";
import Assortment from "../components/homeComponents/Assortment";
import SalesPromotion from "../components/homeComponents/SalesPromotion";
import HotList from "../components/homeComponents/HotList";
import GuideLike from "../components/homeComponents/GuideLike";
import BottomNavBar   from "../components/BottomNavBar";
import { mapState,mapActions,mapMutations } from 'vuex'

import api  from '../server/api'
export default {
    data() {
        return {
        };
    },
    components: {
        TopNavBar,
        SowingMap,
        Assortment,
        SalesPromotion,
        HotList,
        GuideLike,
        BottomNavBar
    },
    computed: {
        ...mapState(["home"])
    },
    methods:{
    ...mapActions(["loadHomeDatas"]),
    },
    mounted() {
        this.loadHomeDatas();
    }
};
</script>
<style lang="less">
@import url("../less/less-var");
@400px: 400px;
.home {
    &-box {
        .wrapper {
            position: absolute;
            top: @topHeight - 1; /*px*/
            bottom: @bottomHeight - 1; /*px*/
            left: 0px;
            right: 0px;
            overflow: hidden;
        }
    }
    &-list {
        width: 100%;
        height: @400px; // dpr 适配时
    }
    &-cuxiao {
        width: 100%;
        height: 560px; // dpr 适配时
        margin-top: 10px;
    }
    &-hotlist {
        width: 100%;
        height: 820px; // dpr 适配时
    }
    
    &-like{
        >.titles {
            text-align: center;
            margin: 40px;
            text-align: center;
            margin: 40px;
            font-size: 32px !important; /*px*/
        }
    }
}
</style>


